import React from 'react'
import { Slider } from './Slider/Slider'
import { TextArea } from './TextArea/TextArea'
import { CalculatorValueI } from '../MainForm'
import './Calculator.scss'

interface PropsI {
  calculatorValue: CalculatorValueI
  setCalculatorValue: (value: CalculatorValueI) => void
  totalAmount: number
}

export const Calculator = ({
  calculatorValue,
  setCalculatorValue,
  totalAmount
}: PropsI) => {
  const onChange = (newValue: CalculatorValueI) => setCalculatorValue(newValue)

  return (
    <div className="calculator">
      <h2 className="calculator_title"> </h2>
      <Slider
        name=" , .."
        value={calculatorValue.square}
        min={0}
        max={250}
        onChange={(value: number) =>
          onChange({ ...calculatorValue, square: value })
        }
      />
      <Slider
        name="  (    ), ."
        value={calculatorValue.lightPoints}
        min={0}
        max={20}
        onChange={(value: number) =>
          onChange({ ...calculatorValue, lightPoints: value })
        }
      />
      <Slider
        name="  (   ), .."
        value={calculatorValue.cornice}
        min={0}
        max={50}
        onChange={(value: number) =>
          onChange({ ...calculatorValue, cornice: value })
        }
      />
      <Slider
        name="  (   ), .."
        value={calculatorValue.secretCornice}
        min={0}
        max={50}
        onChange={(value: number) =>
          onChange({ ...calculatorValue, secretCornice: value })
        }
      />
      <TextArea
        label="  ( ):   , ,   .."
        rows={3}
        onChange={(value: string) =>
          onChange({ ...calculatorValue, additionalInfo: value })
        }
      />
      <div className="calculator_sum">
         :
        <span> {totalAmount}BYN</span>
      </div>
    </div>
  )
}
